import React, { useEffect, useState } from "react";
import NavBar from "../../components/NavBar/NavBar";
import Main from "./Main/Main";
import MainMobile from "./Main/MainMobile/MainMobile";
import Footer from "../../components/Footer/Footer";
import useWindowSize from "../../Hooks/UseWindowSize";

const HowToBakeSubpage = () => {
  const [isMobileProductListOpen, setIsMobileProductListOpen] = useState(false);
  const size = useWindowSize();
  const { width } = size;

  const handleCloseNavbar = () => {
    setIsMobileProductListOpen(false);
  };

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  return (
    <>
      <NavBar
        isMobileProductListOpen={isMobileProductListOpen}
        onClose={handleCloseNavbar}
      />
      {width > 1023 ? <Main /> : <MainMobile />}
      <Footer />
    </>
  );
};

export default HowToBakeSubpage;
